* {
    box-sizing: border-box;
}

html {
    height: 100%;
}

.visible {
    display: block;
    visibility: visible;
    opacity: 1;
    transition: opacity .3s linear, visibility .3s linear;
    z-index: 1;
}


body {
    height: 100%;
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif;
    background-color: #edf1f5;
}

.sticky-footer {
    /* visibility: hidden;
     display: none;*/
    min-height: 70vh;
    margin: 0 auto;
    flex-direction: column;
    justify-content: space-between;
}

.container {
    max-width: 1170px;
}

.heading-1 {
    color: #000000;
    font-size: 28px;
    font-weight: 700;
    line-height: 40px;
    margin: 0;
}

.nav-cards-wrapper {
    max-width: 900px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
}

.nav-card {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
    padding: 35px 15px;
    border: none;
    box-shadow: 4px 7px 35px rgba(198, 211, 214, 0.3);
    border-radius: 8px;
    background-color: #ffffff;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.nav-card:hover {
    text-decoration: none;
    transform: translateY(-10px);
    box-shadow: 4px 7px 35px #c6d3d6;
}

.top-panel {
    height: 58px;
    background-color: #000000;
    color: #fff;
    font-size: 16px;
    font-weight: 700;
    line-height: 1;
    padding: 0 15px;
}

.top-panel__container {
    max-width: 1170px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    margin-left: auto;
    margin-right: auto;
}

.top-panel__phone {
    color: #ffffff;
    text-decoration: none;
}

.top-panel__phone a {
    color: #ffffff;
    text-decoration: none;
}

.top-panel__phone a:hover {
    text-decoration: underline;
}

.btn-show-favourites {
    background-color: transparent;
    border: none;
    color: #fff;
}

.btn-show-favourites i {
    margin-right: 10px;
}

.btn-show-favourites:hover {
    color: #ffdd56;
}

.logo-wrapper {
    padding-top: 60px;
    padding-bottom: 50px;
    text-align: center;
    display: block;
}

.logo {
    display: inline-block;
    margin: 0 auto;
    font-family: 'Oswald', sans-serif;
    transform: skewX(-10deg);
    background-color: #fff;
    padding: 20px 45px 16px;
}

.logo:hover {
    text-decoration: none;
}

.logo__title {
    color: #000000;
    font-size: 48px;
    font-weight: 700;
    line-height: 40px;
}

.logo__subtitle {
    color: #dc0909;
    font-size: 26px;
    font-weight: 700;
    line-height: 40px;
    letter-spacing: 2.86px;
}

.disabled{
    background: #e5e5e5;
    color: #b8b8b8;
    pointer-events: none;
    user-select: none;
}

.filter {
    display: flex;
    margin-top: 35px;
    margin-bottom: 30px;
    box-shadow: 4px 7px 35px rgba(198, 211, 214, 0.3);
    background-color: #ffffff;
    padding: 30px 40px 44px 40px;
    flex-wrap: wrap;
}

.filter__col {
    margin-right: 40px;
}

.filter__col:last-child {
    margin-right: 0px;
}

.filter__label {
    color: #000000;
    font-size: 16px;
    font-weight: 700;
    line-height: 1;
    margin-bottom: 10px;
    display: block;
}

.filter__dropdown {
    width: 200px;
    height: 45px;
    background-color: #e6e6e6;
    padding-left: 10px;
    color: #000000;
    font-size: 16px;
    font-weight: 500;
    line-height: 1;
}

.rooms__wrapper {
    display: flex;
}

.rooms__checkbox {
    margin-top: 7px;
    width: 30px;
    height: 30px;
    left: -1000px;
    top: -1000px;
    cursor: pointer;
}

.rooms__checkbox:checked + .rooms__btn {
    background-color: #e51414;
    color: #fff;
}

.rooms__btn {
    margin-bottom: 0;
    width: 30px;
    height: 45px;
    color: #000000;
    font-size: 16px;
    font-weight: 700;
    margin-right: 10px;
    text-align: center;
    cursor: pointer;
}

.rooms__btn:last-child {
    margin-right: 0px;
}

.rooms__btn:hover {
    opacity: 0.8;
}

.rooms__btn--active {
    background-color: #e51414;
    color: #fff;
}

.range__wrapper {
    display: flex;
    margin-bottom: 0;
}

.range {
    display: flex;
    align-items: center;
    margin-bottom: 0;
}

.range:not(:last-child) {
    margin-right: 14px;
}

.range__label {
    color: #959595;
    font-size: 14px;
    font-weight: 500;
    line-height: 1;
    margin-bottom: 0;
    margin-right: 8px;
}

.range__input {
    margin-right: 5px;
    width: 45px;
    height: 45px;
    border: 1px solid #e6e6e6;
    background-color: #ffffff;
    text-align: center;
    color: #000000;
    font-size: 16px;
    font-weight: 500;
    line-height: 45px;
}

.range__input--price {
    width: 115px;
}

.range__value {
    color: #000000;
    font-size: 14px;
    font-weight: 500;
    line-height: 1;
}

.filter__buttons {
    display: flex;
    justify-content: space-between;
}

.filter__show {
    width: 940px;
    height: 78px;
    border-radius: 8px;
    background-color: #dc0909;
    border: none;
    color: #ffffff;
    font-size: 18px;
    font-weight: 700;
    line-height: 40px;
    transition: 0.1s background-color ease-in;
}

.filter__show:hover {
    background-color: #eb4545;
}

.filter__show:active,
.filter__show:focus {
    outline: none;
    background-color: #c23838;
}

.filter__reset {
    position: relative;
    width: 210px;
    height: 78px;
    padding-right: 24px;
    border: none;
    background-color: transparent;
    color: #b8b8b8;
    font-size: 16px;
    font-weight: 500;
    line-height: 1;
    transition: 0.1s color ease-in;
}

.filter__reset:hover {
    text-decoration: none;
    color: #000;
}

.filter__reset:hover:after {
    opacity: 1;
}

.filter__reset:active,
.filter__reset:focus {
    outline: none;
    transform: translateY(2px);
}

.filter__reset:after {
    position: absolute;
    content: '';
    display: block;
    width: 14px;
    height: 14px;
    right: 10px;
    top: 50%;
    /* background-image: url('../img/icons/multiply-black.png');*/
    background-repeat: no-repeat;
    transform: translateY(-50%);
    opacity: 0.2;
    transition: 0.1s opacity ease-in;
}

.card,
a.card {
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;
    border: none;
    box-shadow: 4px 7px 35px rgba(198, 211, 214, 0.3);
    border-radius: 8px;
    background-color: #ffffff;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
    cursor: pointer;
}

.card:hover,
a.card:hover {
    text-decoration: none;
    transform: translateY(-10px);
    box-shadow: 4px 7px 35px #c6d3d6;
}

.card__img img {
    width: 200px;
    height: 160px;
    object-fit: contain;
}

.card__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 25px 30px 15px;
}

.card__title {
    color: #1c90e9;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.3;
    text-decoration: none;
}

.card__like {
    cursor: pointer;
    font-size: 24px;
    color: #ececec;
}

.card__like:hover {
    color: #ff0000;
}

.card__like--active {
    color: #dc0909;
}

.card__like--active:hover {
    opacity: 0.8;
}

.card__img {
    height: 220px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.card__desc {
    display: flex;
    justify-content: space-between;
    padding: 10px 30px 10px;
}

.card__price {
    display: flex;
    flex-direction: column;
}

.card__price-total {
    color: #dc0909;
    font-size: 24px;
    font-weight: 700;
    line-height: 1;
    margin-bottom: 10px;
}

.card__price-per-meter {
    color: #959595;
    font-size: 14px;
    font-weight: 500;
    line-height: 1;
}

.card__params {
    margin-bottom: 15px;
}

.card__footer {
    display: flex;
    justify-content: space-between;
    padding: 20px 30px 20px;
    border-top: 1px solid #e5e5e5;
}

.card__art {
    color: #959595;
    font-size: 14px;
    font-weight: 500;
}

.card__floor {
    color: #959595;
    font-size: 14px;
    font-weight: 500;
}

.pagination {
    display: flex;
    justify-content: center;
    padding: 40px 0;
}

.pagination__link {
    color: #000000;
    font-size: 16px;
    font-weight: 500;
    line-height: 50px;
    height: 50px;
}

.pagination__link:hover {
    color: #DC0909;
    text-decoration: none;
}

.pagination__link:first-child {
    margin-right: 20px;
}

.pagination__link:last-child {
    margin-left: 20px;
}

.pagination__page {
    width: 60px;
    height: 50px;
    text-align: center;
    color: #000000;
    font-size: 20px;
    line-height: 50px;
    font-weight: 500;
}

.pagination__page:hover {
    color: #DC0909;
    text-decoration: none;
}

.pagination__dots {
    height: 50px;
    text-align: center;
    color: #000000;
    font-size: 20px;
    line-height: 50px;
    font-weight: 500;
    margin-right: 20px;
}

.pagination__page--active {
    box-shadow: 4px 7px 35px rgba(198, 211, 214, 0.3);
    border-radius: 6px;
    background-color: #ffffff;
    font-weight: 800;
}

.view-options {
    height: 107px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.view-options__label {
    color: #000000;
    font-size: 16px;
    font-weight: 500;
    margin-right: 20px;
}

.view-options__select {
    width: 160px;
    height: 40px;
    color: #dc0909;
    color: #000000;
    font-size: 16px;
    font-weight: 500;
    line-height: 40px;
}

.view-options__radio {
    -webkit-appearance: none;
    appearance: none;
    width: 0;
    height: 0;
    position: absolute;
    left: -1000px;
    top: -1000px;
}

.view-options__radio:checked + .view-options__type-item {
    color: #000000;
}

.view-options__type-item {
    margin-right: 12px;
    color: #CFCFCF;
    font-size: 20px;
    text-decoration: none;
    transition: 0.4s color ease;
    cursor: pointer;
}

.view-options__type-item:hover {
    color: #696969;
    text-decoration: none;
}

.view-options__type-item:last-child {
    margin-right: 0;
}

.panels-filter {
    display: flex;
    justify-content: space-between;
    padding-left: 20px;
    padding-right: 20px;
    color: #959595;
    font-weight: 500;
}

.panels-filter__element {
    flex-shrink: 0;
    padding: 10px 0;
    text-align: center;
    margin-right: 15px;
}

.panels-filter__element:last-child {
    margin-right: 0;
}

.panels-filter__name {
    display: inline-block;
    position: relative;
    padding-right: 20px;
    font-size: 12px;
    line-height: 1;
}

.panels-filter__name::after {
    content: '';
    display: block;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    width: 10px;
    height: 5px;
    /* background-image: url('../img/icons/down-arrow.png');*/
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.panels-filter__name:hover {
    cursor: pointer;
    opacity: 0.8;
    color: #dc0909;
}

.panels-filter__name.no-filter::after {
    display: none;
}

.panels-filter__name.no-filter:hover {
    color: #959595;
    cursor: default;
    opacity: 1;
}

.panel,
a.panel {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 73px;
    padding-left: 20px;
    padding-right: 20px;
    margin-bottom: 10px;
    box-shadow: 4px 7px 35px rgba(198, 211, 214, 0.3);
    border-radius: 8px;
    background-color: #ffffff;
    text-align: center;
    color: #000000;
    font-size: 14px;
    font-weight: 500;
    line-height: 1;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.panel:hover,
a.panel:hover {
    cursor: pointer;
    text-decoration: none;
    transform: translateY(-3px);
    box-shadow: 4px 7px 35px #c6d3d6;
}

.panel.panel--no-hover:hover,
a.panel.panel--no-hover:hover {
    cursor: default;
    transform: none;
    box-shadow: 4px 7px 35px rgba(198, 211, 214, 0.3);
}

.panel__artikul {
    width: 120px;
}

.panel__name {
    width: 160px;
}

.panel__block {
    width: 70px;
}

.panel__floor {
    width: 70px;
}

.panel__rooms {
    width: 70px;
}

.panel__sq {
    width: 80px;
}

.panel__price-per-m {
    width: 100px;
}

.panel__price {
    color: #dc0909;
    width: 100px;
}

.panel__seller {
    width: 120px;
}

.panel__favourite {
    width: 100px;
}

.panel__favourite-btn {
    border: none;
    background-color: transparent;
    color: #ececec;
    font-size: 24px;
}

.panel__favourite-btn:hover {
    color: #dc0909;
}

.panel__favourite-btn--active {
    color: #dc0909;
}

.panel__favourite-btn--active:hover {
    color: #ff0000;
    opacity: 0.8;
}

.panel__bidname {
    padding-left: 20px;
    width: 400px;
    text-align: left;
}

.panel__bidphone {
    margin-right: auto;
}

.object {
    margin-top: 30px;
    margin-bottom: 90px;
    height: auto;
    box-shadow: 4px 7px 35px rgba(198, 211, 214, 0.3);
    background-color: #ffffff;
    display: flex;
}

.object__photo {
    position: relative;
    width: 800px;
    border-right: 1px solid #e5e5e5;
}

.object__photo-wrapper {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 80px 0 120px;
}

.object__photo-wrapper img {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
}

.object__photo-next {
    position: absolute;
    right: 50px;
    top: 50%;
    transform: translateY(-50%);
    width: 47px;
    height: 72px;
}

.object__photo-next:hover {
    opacity: 0.8;
    cursor: pointer;
}

.object__photo-prev {
    left: 50px;
    top: 50%;
    position: absolute;
    transform: translateY(-50%);
    width: 47px;
    height: 72px;
}

.object__photo-prev:hover {
    opacity: 0.8;
    cursor: pointer;
}

.object__photo-slider-nav {
    position: absolute;
    bottom: 80px;
    transform: translate(-50%, -100%);
    left: 50%;
}

.object__desc {
    width: 370px;
    padding: 40px 30px;
}

.object__desc-sector {
    color: #1c90e9;
    margin-bottom: 15px;
}

.object__desc-name {
    padding-bottom: 30px;
    margin-bottom: 30px;
    border-bottom: 1px solid #e5e5e5;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.object__desc-title {
    color: #000000;
    font-size: 24px;
    font-weight: 500;
    line-height: 1.5;
    margin-bottom: 5px;
}

.object__desc-art {
    color: #959595;
    font-size: 14px;
    font-weight: 500;
    margin-bottom: 20px;
}

.object__desc-details {
    padding-bottom: 20px;
    margin-bottom: 30px;
    border-bottom: 1px solid #e5e5e5;
}

.details {
    display: flex;
    flex-direction: column;
}

.details__row {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding-bottom: 35px;
}

.details__name {
    color: #959595;
    font-size: 14px;
    font-weight: 500;
    line-height: 1;
}

.details__value {
    color: #000000;
    font-size: 16px;
    font-weight: 500;
    line-height: 1;
}

.details__value--price {
    font-size: 24px;
    font-weight: 600;
    color: #dc0909;
}

.slider-nav {
    display: flex;
    justify-content: center;
}

.slider-nav__dot {
    width: 12px;
    height: 12px;
    background-color: #e5e5e5;
    margin-right: 18px;
    border-radius: 50%;
}

.slider-nav__dot:last-child {
    margin-right: 0;
}

.slider-nav__dot--active {
    width: 12px;
    height: 12px;
    background-color: #e51414;
}

.slider-nav__dot:hover {
    cursor: pointer;
    opacity: 0.8;
}

.params {
    display: flex;
}

.params__item {
    display: flex;
    flex-direction: column;
}

.params__item:not(:last-child) {
    margin-right: 24px;
}

.params__definition {
    color: #959595;
    font-size: 12px;
    font-weight: 500;
    line-height: 1;
    margin-bottom: 15px;
}

.params__value {
    color: #000000;
    font-size: 24px;
    font-weight: 700;
    line-height: 1;
}

.heading-slider {
    color: #000000;
    font-size: 36px;
    font-weight: 700;
    line-height: 1.3;
}

.slider {
    position: relative;
    margin: 30px auto 80px;
    max-width: 1920px;
}

.slider__photo-wrapper img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.slider__control {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 100px;
    height: 140px;
    background-color: #FFF;
    opacity: 1;
}

.slider__control:hover {
    opacity: 0.8;
    cursor: pointer;
}

.slider__control--prev {
    left: 0;
    padding: 34px 30px 34px 24px;
}

.slider__control--next {
    right: 0;
    padding: 34px 24px 34px 30px;
}

.slider__control--disabled {
    opacity: 0.5;
}

.slider__control--disabled:hover {
    opacity: 0.5;
    cursor: default;
}

.slider__control--disabled img {
    opacity: 0.1;
}

.button-favourite {
    display: flex;
    align-items: center;
    justify-content: center;
    width: auto;
    height: 46px;
    padding-left: 20px;
    padding-right: 20px;
    border-radius: 23px;
    border: 3px solid #e5e5e5;
    background-color: #ffffff;
    text-align: center;
    color: #959595;
    font-size: 14px;
    font-weight: 500;
    line-height: 1;
    transition: all 0.2s ease-in;
}

.button-favourite i {
    color: #ececec;
    font-size: 24px;
    margin-right: 10px;
    transition: all 0.2s ease-in;
}

.button-favourite:hover {
    opacity: 0.8;
    cursor: pointer;
    border-color: #e51414;
    color: #e51414;
}

.button-favourite:hover i {
    color: #e51414;
}

.button-favourite:focus {
    outline: none;
}

.button-favourite--active {
    border-color: #ff9898;
    background-color: #dc0909;
    color: #fff;
}

.button-favourite--active i {
    color: #fff;
}

.button-favourite--active:hover {
    opacity: 0.8;
    cursor: pointer;
    color: #fff;
}

.button-favourite--active:hover i {
    color: #fff;
}

.button-order {
    display: block;
    height: 68px;
    padding: 0;
    width: 100%;
    border: none;
    border-radius: 32px;
    background-color: #e51414;
    margin-bottom: 10px;
    text-align: center;
    color: #ffffff;
    font-size: 22px;
    font-weight: 700;
    line-height: 1;
    transition: all 0.2s ease-in;
}

.button-order:hover {
    opacity: 0.8;
    cursor: pointer;
    background-color: #ff0000;
}

.button-order:active,
.button-order:focus {
    outline: none;
    opacity: 1;
    cursor: pointer;
    background-color: #971111;
}

.button-preview {
    display: block;
    height: 68px;
    width: 100%;
    border-radius: 32px;
    border: 3px solid #e5e5e5;
    background-color: #ffffff;
    padding: 0;
    text-align: center;
    color: #000000;
    font-size: 18px;
    font-weight: 700;
    line-height: 1;
}

.button-preview:hover {
    opacity: 0.8;
    cursor: pointer;
    background-color: #e7e7e7;
}

.back-to-results {
    display: inline-block;
    width: 100%;
    height: 78px;
    box-shadow: 4px 7px 35px rgba(198, 211, 214, 0.3);
    border-radius: 8px;
    background-color: #ffffff;
    text-align: center;
    color: #000000;
    font-size: 18px;
    font-weight: 700;
    line-height: 1;
    padding: 0;
}

.back-to-results:hover {
    opacity: 0.8;
    text-decoration: none;
}

.footer {
    margin-top: 80px;
    background-color: #000000;
    padding: 20px 0;
    text-align: center;
    color: #ffffff;
    font-size: 16px;
    font-weight: 600;
    line-height: 1.3;
}

.footer p {
    margin-top: 0;
    margin-bottom: 0;
}

.footer a {
    text-decoration: underline;
